<template>
	<div @touchstart="touchStart($event)">
		<div class='navHeader'>
			<div class="header">
				<router-link to="https://m.baidu.com/" class="float-left baidu-icon"></router-link>
				<router-link to="/me" class="float-left profile-icon"></router-link>
				<router-link to="/read" class="float-right subscribe-icon"></router-link>
				<router-link to="/serach" class="float-right search-icon"></router-link>
			</div>
			<navz @listenboy='foo'></navz>
		</div>

		<div class='listindex'>

			<div class="zbox"></div>
			<banner></banner>
			<router-link :to="{path:'detail',query: {id:index}}" class='indexList' v-for='(item,index) in list'>
				<div class='left'><img :src="item.image" /></div>
				<div class='right'>
					<p>{{item.title}}</p>
					<span>1天前</span>
					<i>热点</i>
				</div>
			</router-link>
			<div class="go" :style="{height:top+'px',lineHeight:top+'px'}" v-show='isShow'>{{wait}}</div>
		</div>

	</div>
</template>
<script>
	import Vue from 'vue'
	import navz from './plug-in/navz'
	import banner from './plug-in/banner'
	import VueScroller from 'vue-scroller'
	import axios from 'axios'
	Vue.use(VueScroller);
	window.scrollTo(0,document.body.scrollHeight)
	var z = 0
	//初始化参数
	var start = 0
	var end = 10
	//上拉参数
	var startY
	export default {
		components: {
			navz,
			banner

		},
		data() {
			return {
				isShow: true,
				noData: '',
				list: [],
				top: 50,
				wait: '上滑加载更多'
			}
		},

		mounted() {

			if(this.list.length == 0) {
				start = 0
				end = 10
			}
			axios.get('/newlist', {
					//传递参数
					parmas: {
						start,
						end
					}
				}).then((response) => {
				

					this.list = this.list.concat(response.data.newList)
					console.log(response.data.newList)
					if(response.data.newList < 10) {
						this.wait = "没有更多数据";
						
					}
				})
				.catch(function(error) {
					console.log('失败')
				});

		},
		methods: {

			foo(i) {

			},
			touchStart(v) {
				this.isShow = true
				var scrollHeight = document.body.scrollHeight
				var scrollTop = document.body.scrollTop
				var offsetHeight = document.body.offsetHeight
				startY = v.touches[0].pageY
				if(offsetHeight + scrollTop == scrollHeight) {

				start = start + 10
				end = end + 10
				axios.get('/newlist', {
						//传递参数
						parmas: {
							start,
							end
						}
					}).then((response) => {
						console.log('获取一次数据');

						this.list = this.list.concat(response.data.newList)

						if(response.data.newList < 10) {
							this.wait = "没有更多数据";
						}
					})
					.catch(function(error) {
						console.log('失败')
					});
				}
			}

		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	.navHeader {
		height: 0.9rem;
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 100;
	}
	
	.navHeader-fixed {
		height: 0.9rem;
		width: 100%;
	}
	
	.header {
		background: #3e98f0 url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/bg-news-logo_344ce44.png) no-repeat;
		background-position: center 0.1rem;
		background-size: 0.65rem auto;
		width: 100%;
		height: 0.48rem;
		background-color: #3e98f0;
		border-bottom: 0.01rem solid #5dabf0;
		position: relative;
		z-index: 1000;
	}
	
	.header .baidu-icon {
		background-position: center 6px;
	}
	
	.header a {
		width: 34px;
		height: 100%;
	}
	
	.header .baidu-icon {
		width: 34px;
		height: 100%;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -105px;
	}
	
	.header .profile-icon {
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -461px;
	}
	
	.header .subscribe-icon {
		margin-right: 8px;
		width: 34px;
		height: 100%;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -105px;
	}
	
	.header .search-icon {
		margin-right: 10px;
		width: 34px;
		height: 100%;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -160px;
	}
	
	body .float-left {
		float: left;
		margin-left: 8px;
	}
	
	body .float-right {
		float: right;
		margin-right: 8px;
	}
	
	.nav {
		position: relative;
		height: 0.4rem;
		width: 12rem;
		background: #3e98f0;
		right: 0;
	}
	
	.zbox {
		height: 0.9rem;
		width: 100%;
	}
	
	.Test {
		position: absolute;
		right: 0;
		top: 0.5rem;
		width: 0.45rem;
		height: 0.38rem;
		background: linear-gradient( to left, rgba(62, 152, 240, 1), rgba(62, 152, 240, 0.6), rgba(62, 152, 240, 0.2), rgba(62, 152, 240, 0))
	}
	
	.listindex a {
		height: 0.75rem;
		width: 90%;
		margin: 0 auto;
		border-bottom: 0.01rem solid #eee;
		display: block;
		padding: 0.1rem 0;
	}
	
	.indexList .left {
		height: 0.75rem;
		width: 1.15rem;
		background: pink;
		float: left;
	}
	
	.indexList .left img {
		height: 0.75rem;
		width: 1.15rem;
	}
	
	.indexList .right {
		float: right;
		margin-left: 0.2rem;
		font-size: .18rem;
		line-height: .26rem;
		color: #333;
	}
	
	.indexList .right p {
		height: 0.53rem;
		width: 2rem;
		
	}
	
	.indexList .right span {
		display: inline-block;
		color: #999;
		font-size: .13rem;
		margin-right: 5px;
		font-weight: 400;
		float: left;
	}
	
	.indexList .right i {
		font-style: normal;
		border: 1px solid #efb9b9;
		height: 0.15rem;
		width: 0.24rem;
		border-radius: 0.02rem;
		display: inline-block;
		float: left;
		display: block;
		margin-top: 0.04rem;
		text-align: center;
		line-height: 0.15rem;
		color: #ef3f41;
		font-size: 0.1rem;
	}
	
	.go {
		width: 100%;
		height: 50px;
		font-size: 0.16rem;
		color: #999;
		overflow: hidden;
		line-height: 50px;
		text-align: center;
	}
</style>


