<template>
	<div class="navc">
		<ul class='navUl'>
			<li :class="{ active: item.isActive}" v-for='item in classdata' @click='tab(item)'>{{item.title}}</li>
			<p>换一换</p>
		</ul>
		<ul class="list">
			<li v-for='(item,index) in list'>{{item.title}} <span class='addicon' @click='addicon(item,index)' :class="{black: item.add}"></span></li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'navc',
		data() {
			return {
				list: []
				

			}
		},
		props:{
			classdata:{
				type:Array,
				default:() => [{
					title: '媒体',
					isActive: true,
					list: [{
						title: "媒体网",
						add: false
					}]
				}, {
					title: '频道',
					isActive: false,
					list: [{
						title: "频道网",
						add: false
					}]
				}, {
					title: '话题',
					list: [{
						title: "话题",
						add: false
					}]
				}],
			}
		},
		mounted() {
			this.list = this.classdata[0].list
			console.log(this.classdata)
		},
		methods: {
			tab(i) {
				this.classdata.forEach((v) => {
					v.isActive = false
				})
				i.isActive = true
				this.list = i.list
			},
			addicon(item,i){
				item.add = !item.add
			
			}
		}
	}
</script>

<style>
	.navUl {
		width: 90%;
		height: 0.4rem;
		margin: 0 auto;
		border-bottom: 1px solid #f6f6f6;
	}
	
	.navUl li {
		float: left;
		line-height: 0.38rem;
		list-style: none;
		padding: 0 0.05rem;
		font-size: 0.16rem;
		margin-right: 0.05rem;
	}
	
	.navUl p {
		float: right;
		line-height: 0.4rem;
		list-style: none;
		padding: 0 0.1rem;
		font-size: 0.16rem;
	}
	
	.navUl .active {
		color: #239bf0;
		border-bottom: 2px solid #239bf0;
	}
	
	.list {
		width: 90%;
		margin: 0 auto;
	}
	
	.list li {
		border: #f6f6f6 0.01rem solid;
		border-width: 0 0 0 1px;
		position: relative;
		display: block;
		overflow: hidden;
		padding-left:0.08rem;
		line-height:0.4rem;
		height: 0.4rem;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-size: .16rem;
		
		color: #4f5256;
		float: left;
		width: 47%;
		margin-top: 0.02rem;
		border-bottom: 0.01rem solid #f6f6f6;
	}
	
	.list li:nth-child(2n+1) {
		border-left: 0;
	}
	
	.addicon {
		height: 40px;
		width: 40px;
		background-image: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/bg-subscribe_3be23c4.png);
		background-position: 10px -14px;
		background-size: 20px auto;
		background-repeat: no-repeat;
		position: absolute;
		top: 0;
		right: 0;
	}
	.black{	
    	background-position: 10px -46px;
	}
</style>


