<template>
	<transition name="fade">
		<div class='box' v-show="isShow">
			<div class="bgbule">
				<router-link to='/' class='span'></router-link>
				<div class="title">搜新闻</div>
			</div>
			<div class='input-box'>
				<input type="text" v-model="val" class='input5'/>
				<img src="./icon/xxx.png" @click='clear'/>
				<div class="serach">百度一下</div>
			</div>
			<div class="login">
				
					<transition name="bindf">
						<div class="bind" :style="{left:run+'px'}"></div>
					</transition>
			</div>
			
			
		</div>
	</transition>
</template>

<script>
	export default {
		data() {
			return {
				isShow: false,
				val:'',
				run:0
			}
		},
		mounted() {
			this.init();
			setInterval(()=>{
				this.run++
				if(this.run ==120){
					this.run=0
				}
			},10)
		},
		methods: {
			init() {
				setTimeout(() => {
					this.isShow = true;
				}, 100)

			},
			clear(){
				this.val=''
			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	.box {
		height: 6rem;
		width: 100%;
		position: fixed;
		transition: all .3s;
	}
	
	.bgbule {
		background-color: #239bf0;
		width: 100%;
		height: 0.48rem;
	}
	
	.fade-enter-active,
	.fade-leave-active {
		left: 0%;
	}
	
	.fade-enter,
	.fade-leave-active {
		left: 100%;
	}
	
	.span {
		display: block;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -2.16rem;
		height: 0.44rem;
		width: 0.34rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	
	.title {
		font-size: 0.16rem;
		line-height: 0.48rem;
		width: 75%;
		color: #fff;
		text-align: center;
		margin: 0 auto;
	}
	.input5 {
		height: 0.44rem;
		border: 0;
		width: 2.2rem;
		outline: none;
		font-size: 16px;
		float: left;
		padding-left: 0.05rem;
	}
	
	.input-box {
		margin: 0.15rem 0.17rem 0;
		z-index: 999;
		height: 0.44rem;
		line-height: 0.44rem;
		border: 0.01rem solid #ececec;
	}
	
	.input-box img {
		height: 0.2rem;
		width: 0.2rem;
		z-index: 100;
		margin: 0.12rem 0 0 0.05rem;
		float: left;
	}
	
	.input-box .serach {
		height: 0.44rem;
		border-left: 1px solid #ececec;
		z-index: 0;
		background: #fcfcfc;
		line-height: 0.44rem;
		width: 0.83rem;
		text-align: center;
		font-size: .16rem;
		float: right;
	}
	
	.login {
		position: relative;
		height:0.6rem;
		width: 1.5rem;
		display: inline-block;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/new-loading-logo_455e47c.png) 0 0 no-repeat;
		background-size: 100%;
		opacity: .7;
		position: absolute;
		left:50%;
		top:50%;
		margin-left:-0.75rem;
		margin-top:0.3rem;
		
	}
	.bind{
		height:0.6rem;
		width:0.3rem;
		background: #fff;
		transform:rotate(-9deg);
		opacity: .5;
		position: absolute;
		
	}
	
	.bindf-enter-active,
	.bindf-leave-active {
		left: 100%;
	}
	
	.bindf-enter,
	.bindf-leave-active {
		left: 0%;
	}
</style>


