<template>
	<transition name="fade">
		<div class='box' v-show="isShow" transiton="fade">
			<div class="bgbule">
				<router-link to='/' class='span'></router-link>
				<div class="headpic">
					<p><img src="https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/person_dcc8a01.png"></p>
				</div>
				<div class="login">立即登录</div>
			</div>
			<ul>
				<li>
					<i class='icon1'></i> 我的消息
				</li>
				<li>
					<i class='icon2'></i> 我的收藏
				</li>
				<li>
					<i class='icon3'></i> 无图模式
					<i class='icon4' @click='click' :class='{active:isChange}'></i>
				</li>
			</ul>
			
		</div>
	</transition>
</template>

<script>
	export default {
		data() {
			return {
				isShow: false,
				isChange:false
			}
		},
		mounted() {
			this.init();
		},
		methods: {
			init() {
				setTimeout(() => {
					this.isShow = true;
				}, 100)
				
			},
			click(){
				this.isChange = ! this.isChange
			}
			
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	.box {
		height: 4rem;
		width: 100%;
		position: fixed;
		transition: all .3s;
	}
	
	.bgbule {
		background-color: #239bf0;
		width: 100%;
		height: 2.22rem;
	}
	
	.fade-enter-active,
	.fade-leave-active {
		left: 0%;
	}
	
	.fade-enter,
	.fade-leave-active {
		left: 100%;
	}
	
	.span {
		display: block;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -2.16rem;
		height: 0.44rem;
		width: 0.34rem;
	}
	
	.headpic {
		width: 100%;
		height: 0.88rem;
	}
	
	.headpic p {
		height: 0.8rem;
		width: 0.8rem;
		margin: 0 auto;
		border: 0.01rem solid #fff;
		border-radius: 50%;
	}
	
	.headpic img {
		height: 0.8rem;
		width: 0.8rem;
		border: 0;
		display: block;
	}
	
	.login {
		height: 0.38rem;
		width: 3.75rem;
		font-size: .18rem;
		color: #fff;
		line-height: .36rem;
		border: 1px solid #fff;
		border-radius: 3px;
		background-color: #39a5f2;
		display: block;
		height: 36.3px;
		width: 98.7px;
		margin: 0 auto;
		margin-top: 0.22rem;
		text-align: center;
	}
	
	ul li {
		border-bottom: 1px solid #f6f6f6;
		padding: 15px 0;
		color: #797979;
		font-size: .16rem;
		line-height: .16rem;
		width: 92%;
		margin: 0 auto;
		list-style: none;
	}
	
	.icon1 {
		background-position: -3px 3px;
	}
	
	.icon2 {
		background-position: -3px -32px;
	}
	
	.icon3 {
		background-position: -3px -72px;
	}
	
	ul li i {
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/profile-icons_29e9e67.png) no-repeat;
		display: inline-block;
		width: 20px;
		height: 27px;
		vertical-align: -7px;
		margin-right: 7px;
		background-size: 20.5px auto;
	}
	
	.icon4 {
		float:right;
		display: inline-block;
		width: 0.2rem;
		height: 0.27rem;
		vertical-align: -0.07rem;
		margin-right: 0.07rempx;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/profile-icons_29e9e67.png) no-repeat;
		background-size: 20.5px auto;
		background-position: 1px -170px;
	}
	.active{
		background-position: 1px -138px;
	}
</style>


