<template>
	<div class='detailbox'>
		<h2>{{title}}</h2>
		<div ref='txt' class='detail'>{{detail}}</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data() {
			return {
				detail: '',
				title: '',
			}
		},
		mounted() {
			let detailId = this.$route.query.id
			console.log(this.$route.query.id)
			axios.get('/newlist/detail', {
				//传递参数
				parmas: {
					id: detailId
				}
			}).then((response) => {
				console.log(response.data.detail.detail)
				var txt = response.data.detail.detail
				this.title = response.data.detail.title
				var newtext = txt.replace(/\[~]+|[~]+/g, '</p><p>')
				this.detail = '<p>' + newtext + '</p>'
				this.$refs.txt.innerHTML = this.detail
			}).catch(function(error) {
				console.log('失败')
			});
		}
	}
</script>

<style>
	h1 {
		text-align: left;
		
		color: #333;
		margin-bottom: .11rem;
		font-size: .24rem;
		line-height: 1.5rem;
	}
	
	.detail{
		font-size: .18rem;
		position: relative;
		color: #333;
		text-align: left;
	}
	.detailbox{
		width:95%;
		margin:0 auto;
	}
</style>


