<template>
	<div class="swiper-container swiper-container2">
		<div class="swiper-wrapper swiper-wrapper2">
			<div class="swiper-slide swiper-slide2" v-for='item in imgdata'><img :src='item.imgsrc'/></div>
		</div>
		<!-- Add Pagination -->
		<div class="swiper-pagination"></div>
		<div class="swiper-paginationx"></div>
	</div>
</template>

<script>
	export default {

		data() {
			return {
				imgdata:[{imgsrc:'https://timg01.bdimg.com/timg?tc&size=b755_472&sec=0&quality=75&cut_x=94&cut_y=0&cut_h=0&cut_w=755&di=8b4fa2ed7c801582c57ece38c3c08f9d&src=http%3A%2F%2Fimgsa.baidu.com%2Fnews%2Fcrop%253D30%252C15%252C944%252C472%2Fsign%3D7bd31d569225bc313f125bd863ecbd9d%2Fb7003af33a87e950630c3bf41b385343fbf2b40b.jpg',alt:'123'},{imgsrc:'https://timg01.bdimg.com/timg?tc&size=b588_368&sec=0&quality=75&cut_x=73&cut_y=0&cut_h=0&cut_w=588&di=9fdc7b4b44fc60018efcf81ac38aea33&src=http%3A%2F%2Fimgsa.baidu.com%2Fnews%2Fcrop%253D33%252C47%252C736%252C368%2Fsign%3Dae4cf5b29616fdfacc239cae89bcbf78%2F1b4c510fd9f9d72a69fa1e84df2a2834359bbb88.jpg',alt:'123'},{imgsrc:'https://timg01.bdimg.com/timg?tc&size=b539_337&sec=0&quality=75&cut_x=67&cut_y=0&cut_h=0&cut_w=539&di=8cf9b0c9616cafe2c9ca8c87046a9c50&src=http%3A%2F%2Fimgsa.baidu.com%2Fnews%2Fcrop%253D0%252C74%252C674%252C337%2Fsign%3D2c724fa04036acaf4dafccbc41e9a127%2F10dfa9ec8a136327854dceaa9a8fa0ec09fac783.jpg',alt:'123'}]
			}
		},
		mounted() {
			var swiper2 = new Swiper('.swiper-container2', {
				autoplay: {
					delay: 3000, //5秒切换一次
					
				},
				
				pagination: {
					el: '.swiper-pagination',
				},
			});
			
		},
		methods: {

		}
	}
</script>

<style>
	.swiper-slide2 {
		text-align: center;
		font-size: 18px;
		background: #fff;
		width: 100%;
		/* Center slide text vertically */
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
		height:2.1rem;
		z-index:-2;
	}
	.swiper-slide2 img{
		width: 100%;
		height:100%;
		border:0;
	}
	.swiper-container2{
		margin-bottom:0.05rem;
	}
</style>


