<template>
	<div class="swiper-container nav swiper-container1">
		<div class="swiper-wrapper">
			<div class="swiper-slide swiper-slide1" v-for="item in nav" id='navitem' @click='boy(item.path,item)' :class='{bottom:item.isbottom}'>{{item.nav}}</div>
		</div>
		<!-- Add Pagination -->
	</div>

</template>

<script>
	export default {

		data() {
			return {
				nav: [{
					nav: '推荐',
					path: '123',
					isbottom:true
				}, {
					nav: '百家',
					path: '234',
					isbottom:false
				}, {
					nav: '本地',
					path: '345',
					isbottom:false
				}, {
					nav: '图片',
					path: '123',
					isbottom:false
				}, {
					nav: '娱乐',
					path: '123',
					isbottom:false
				}, {
					nav: '社会',
					path: '123',
					isbottom:false
				}, {
					nav: '军事',
					path: '123',
					isbottom:false
				}, {
					nav: '互联网',
					path: '123',
					isbottom:false
				}, {
					nav: '科技',
					path: '123',
					isbottom:false
				}, {
					nav: '女人',
					path: '123',
					isbottom:false
				}, {
					nav: '搞笑',
					path: '123',
					isbottom:false
				}, {
					nav: '生活',
					path: '123',
					isbottom:false
				}]
			}
		},

		mounted() {
			var swiper1 = new Swiper('.swiper-container1', {
				freeMode: true,
				freeModeMomentum: false,

				width: 55,

				pagination: {

					el: '.swiper-pagination',
					clickable: true,

				},

			})

		},
		methods: {
			boy(i,item) {
				this.$emit('listenboy', i)
				this.nav.forEach((v)=>{
					v.isbottom = false
				})
				item.isbottom = true
			}
		}
	}
</script>

<style>
	.nav {
		position: relative;
		height: 0.4rem;
		width: 100%;
		background: #3e98f0;
		right: 0;
	}
	
	#navitem {
		float: left;
		font-size: .16rem;
		display: inline-block;
		text-align: center;
		line-height: 0.4rem;
		width: 0.5rem;
		margin-left: 0.1rem;
		margin-right: 0.1rem !important;
		text-decoration: none;
		color: #fff;
		opacity: .7;
	}
	
	.Test {
		position: absolute;
		right: 0;
		top: 0.5rem;
		width: 0.45rem;
		height: 0.38rem;
		background: linear-gradient( to left, rgba(62, 152, 240, 1), rgba(62, 152, 240, 0.6), rgba(62, 152, 240, 0.2), rgba(62, 152, 240, 0))
	}
	
	#navz {
		font-size: 20px;
	}
	
	.swiper-slide1 {
		text-align: center;
		font-size: 18px;
		/* Center slide text vertically */
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
		background: rgba(0, 0, 0, 0);
		
	}
	.bottom{
		border-bottom:0.02rem solid #fff;
		box-sizing: border-box;
	}
	
</style>


