import Vue from 'vue'
import Router from 'vue-router'
import newIndex from '@/components/newIndex'
import me from '@/components/me'
import serach from '@/components/serach'
import read from '@/components/read'
import readserach from '@/components/readserach'
import detail from '@/components/detail'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			component: newIndex
		}, {
			path: '/detail',
			component: detail
		},
		{
			path: '/me',
			component: me
		},
		{
			path: '/serach',
			component: serach
		},
		{
			path: '/read',
			component: read
		},
		{
			path: '/readserach',
			component: readserach
		}
	]
})


