<template>
	<div>
		<div class='box' v-show="isShow">
			<div class="bgbule2">
				<router-link to='/read' class='span2'></router-link>
				<div class="title">搜索订阅</div>
				<div class="right">完成</div>
			</div>
				<input type="text" placeholder="输入关键词搜索即可" class='inp' v-model='aval' @keydown='add'/>
				<img src="./icon/xxx.png" @click='clear'class='aimg' v-if='xshow'/>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isShow: false,
				xshow:false,
				aval:''
			}
		},
		mounted() {
			this.init(),
			this.add()
		},
		methods: {
			init() {
				setTimeout(() => {
					this.isShow = true;
				}, 100)

			},
			clear(){
				this.aval = '';
				this.xshow = false
			},
			add(){
				if(this.aval.length>0){
					this.xshow = true
				} 
				if(this.aval.length==0){
					this.xshow = false
				}
			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	.box2 {
		height: 6rem;
		width: 100%;
		position: fixed;
		transition: all .3s;
	}
	
	.bgbule2 {
		background-color: #239bf0;
		width: 100%;
		height: 0.48rem;
	}
	
	.fade-enter-active,
	.fade-leave-active {
		left: 0%;
	}
	
	.fade-enter,
	.fade-leave-active {
		left: 100%;
	}
	
	.span2 {
		display: block;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -2.16rem;
		height: 0.44rem;
		width: 0.34rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	
	.title {
		font-size: 0.16rem;
		line-height: 0.48rem;
		width: 75%;
		color: #fff;
		text-align: center;
		margin: 0 auto;
	}
	
	.inp {
		width:85%;
		padding-left:0.3rem;
		background-image: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/bg-simple-searchbox_fc34bc7.png);
		background-size: 0.25rem 0.75rem;
		background-repeat: no-repeat;
		background-position: 0.09rem 0.08rem;
		border: 0.01rem solid #ececec;
		font-size: .16rem;
		color: #b8bac0;
		position: absolute;
		top:0.6rem;
		left:2%;
		outline: none;
		height: 0.36rem;
	}
	.right{
		font-size: 0.15rem;
		color:#fff;
		position: absolute;
		right:0.2rem;
		top:0.15rem;
	}
	.aimg{
		position: absolute;
		right:0.3rem;
		top:0.7rem;
	}
</style>


