var Mock = require('mockjs')
var newList = []
var detail = []
var Random = Mock.Random
    for(var i=0;i<24;i++){
    	newList.push(Mock.mock({'title|1': '@cparagraph(1)','image':Random.image('200x100')}))
    }
    detail = newList
    for(var i=0;i<24;i++){
    	function random(num1,num2){
			return Math.floor(Math.random()*(num2-num1)+num1)
		}
    	var num = random(20,30)
    	var str = Random.cparagraph()+'~'
    	for(let i=0;i<num;i++){
    		
    		if(i==num-1){
    			str = str + Random.cparagraph()
    		}else{
    			str = str + Random.cparagraph()+'~'
    		}
    	}
    	
    	detail[i].detail = str
    }
var mockdata = {newList,detail}
export default mockdata



