<template>
	<transition name="fade">
		<div class='box1' v-show="isShow">
			<div class="bgbule1">
				<router-link to='/' class='span1'></router-link>
				<div class="title">订阅中心</div>
			</div>
			<router-link to='/readserach'><input type="text" placeholder="输入关键词搜索即可" class='inp2'/></router-link>
			
			<navc class='navc'></navc>
		</div>
		
		
	</transition>
</template>

<script>

	import navc from './plug-in/navc'
	
	export default {
		
		components: {
			navc
		},
		data() {
			return {
				isShow: false
			}
		},
		mounted() {
			this.init();
		},
		methods: {
			init() {
				setTimeout(() => {
					this.isShow = true;
				}, 100)

			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	.box1 {
		height: 6rem;
		width: 100%;
		position: fixed;
		transition: all .3s;
	}
	
	.bgbule1 {
		background-color: #239bf0;
		width: 100%;
		height: 0.48rem;
	}
	
	.fade-enter-active,
	.fade-leave-active {
		left: 0%;
	}
	
	.fade-enter,
	.fade-leave-active {
		left: 100%;
	}
	
	.span1 {
		display: block;
		background: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/icon-bg_6e9bc53.png) no-repeat;
		background-size: 40px 500px;
		background-position: center -2.16rem;
		height: 0.44rem;
		width: 0.34rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	
	.title {
		font-size: 0.16rem;
		line-height: 0.48rem;
		width: 75%;
		color: #fff;
		text-align: center;
		margin: 0 auto;
	}
	
	.inp2 {
		width:80%;
		padding: 15px 0 15px 38px;
		background-image: url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/bg-simple-searchbox_fc34bc7.png);
		background-size: 25px 75px;
		background-repeat: no-repeat;
		background-position: 9px 12px;
		border: 1px solid #ececec;
		font-size: .16rem;
		line-height: .16rem;
		color: #b8bac0;
		position: absolute;
		top:0.7rem;
		left:5%;
		outline: none;
	}
	.navc{
		margin-top:20%;
	}
</style>


