var axios = require('axios');
var MockAdapter = require('axios-mock-adapter');
var mock = new MockAdapter(axios);
import mockdata from './mock.js'

let _newList = mockdata.newList
let _detail = mockdata.detail
let start1 = 0
let end1 = 10
export default {
	init() {
		//新闻列表
		mock.onGet('/newlist').reply(config => {
			
			let start = config.parmas.start
			let end = config.parmas.end
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, {
						newList:_newList.slice(start,end)
					}]);
				},200);
			})
		})
		//详细内容
		mock.onGet('/newlist/detail').reply(config => {
			let id = config.parmas.id
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, {
						detail:_detail[id]
					}]);
				},200);
			})
		})
	}
}


